.about {
  @include page-grid;

  &__title {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 4;
      font-size: 48px;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__content {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 4 / 7;
    }

    @include on-desktop {
      grid-column: span 7;
    }
  }

  &__text {
    margin: 0;
    font-size: 23px;
    font-weight: 500;
    line-height: 30px;
  }
}
